.wrpper {
  width: 100%;
  height: 100px;
  background-color: rgb(60, 60, 136);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: -1px 3px 16px -2px rgba(0,0,0,0.52);
  z-index: 100;

  span {
    margin-left: 1rem;
    font-size: 1.5rem;

    @media (max-width: 240px) {
      display: none;
    }

    @media (min-width: 500px) {
      font-size: 2rem;
      margin-left: 3rem;
    }
  }

  ul {
    list-style: none;
    margin-right: 1rem;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0px;
    padding-inline-start: 0px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    :hover {
    background-color: #464691;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    }
    :focus {
    text-decoration: underline;
    }
    
    @media (min-width: 500px) {
      margin-right: 3rem;
    }
  }
  
  li {
    margin-left: 1rem;

    @media (max-width: 200px) {
      font-size: small;
    }
  }
  
  a {
    text-decoration: none;
    color: white;
    
    @media (min-width: 500px) {
      font-size: large;
    }
  }
}